import { UObject } from "../Core/UObject";
import { Bounds } from "../Math/Bounds";
import { Vector2 } from "../Math/Vector2";
import { Vector3 } from "../Math/Vector3";
import { Vector4 } from "../Math/Vector4";

export class Mesh extends UObject {
    public bounds: Bounds[];                       // 
    public material: string[];                     // 
    public triangles: number[];                    // 
    public faceNormals: Vector3[];                 // 
    public faceCenters: Vector3[];                 // 
    public vertices: Vector3[];                    // 
    public uv: Vector2[];                          // UV
    public normals: Vector3[];                     // 
    public tangents: Vector4[];                    // 
    public subMeshes: SubMesh[];                   // 

    public constructor() {
        super();
        this.bounds = [];
        this.material = [];
        this.triangles = [];
        this.faceNormals = [];
        this.vertices = [];
        this.uv = [];
        this.normals = [];
        this.tangents = [];
        this.subMeshes = [];
    }

    // 
    public checkValid(): Boolean {
        // uv
        return this.vertices.length !== 0
            && this.vertices.length === this.uv.length
            && this.vertices.length === this.normals.length
            && this.triangles.length !== 0
            && this.triangles.length % 3 === 0
            && this.faceNormals.length === this.faceCenters.length
            && this.faceNormals.length * 3 === this.triangles.length;
    }

    // 
    public recalculateBounds() {
        //TODO
    }

    public onDestroy(): void {
        throw new Error("Method not implemented.");
    }
}

export class SubMesh {
    public vertexCount: number;
    public firstVertex: number;
    public indexCount: number;
    public indexStart: number;
    public bounds: Bounds;
    public material: string;

    public constructor() {
        this.vertexCount = 0;
        this.firstVertex = 0;
        this.indexCount = 0;
        this.indexStart = 0;
        this.bounds = new Bounds();
        this.material = "";
    }
}